import React from 'react'
import { Card, Tag } from 'antd'
import './block.css'
import Avatar from 'antd/lib/avatar/avatar'
import Utils from '../../utils/Utils'
import { WriteHtml } from './WriteHtml'

/**
 * 
 * @param {参数为一个对象} object 格式如下
 * {
 *    className: 'xxx' 不是必输的如果不输入的话使用默认的格式
 *    avatar: 'xxx' 这个是头上图片的地址不是必输如果不输入使用默认的值
 *    source: 'xx' 来源 这个是头上右边的tags属性，如果不输入就不会显示
 *    context: 'xx' 为中间的内容区域改参数可以是文本也可以是html格式形式
 *    keywords: [
 *    keyword: 'xx'   一个关键词数组对象为底部的关键词  
 * ]
 * }
 */
export const blockCard = (object={}) => {
    return (
        <div className={object.className ? object.className : "block-card"} onClick={()=>{
            window.open(object.linkUrl)
        }}>
            <Card size="small" title={<div className="card-model"><div><Avatar src={object.avatar ? object.avatar : 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'}/> </div><div className="card-title">{object.title ? object.title : ''}</div></div>} 
            extra={ object.source ? <Tag color={Utils.getColors()}>{object.source}</Tag> : ''} style={{ width: "100%",height: '100%' }}
            actions={[
                <div className="card-footer">
                    <div>关键词：</div>
                    <div className="card-keyword">
                     {object.keywords? object.keywords.map((item,index)=>
                         <Tag color={Utils.getColors()} key={index}>{item.keyword}</Tag>)
                     :'' }
                       
                    </div>
                </div>
              ]}>
               {object.context ? WriteHtml({className:"card-context", message:object.context}) : ''}
            </Card>
        </div>
    )
}