/**
 * 项目通用的Utils算法，适用于整个项目
 */
export default class Utils{

    /**
     * 得到随机的颜色值
     */
    static getColors(){
     const colors= ['red', 'magenta', 'volcano', 'orange', 'gold' , 
                   'lime' , 'green', 'cyan', 'blue', 'geekblue', 
                   'purple', '#f50', '#2db7f5', '#87d068', '#108ee9'];
      return colors[Math.floor(Math.random()*(colors.length))]
    }
}