import React, { Component } from "react";
import './index.css'
import { Carousel, Layout, Menu } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import { HashRouter, Route, Switch, Link } from 'react-router-dom';
import Axios from 'axios'
import SoftInstall from "./component/SoftInstall";
import GitCommend from "./component/GitCommend";
import WebSource from "./component/WebSource";
import UiComponment from "./component/UiComponment";
import UtilsComponment from "./component/UtilsComponment";
import HzeroComponment from "./component/HzeroComponment";
import Problem from "./component/Problem";
import Introduce from "./component/Introduce";
import UseInfo from "./component/UseInfo";
import Commit from "./component/Commit";
const { Header, Sider, Content } = Layout;
const { SubMenu } = Menu;


export default class titContent extends Component {
    formRef = React.createRef();
    constructor(props) {
        super(props);
        this.state = {
            carouselInfo: [],
        }
    }

    getCarousel() {
        return <Carousel autoplay >
            {this.state.carouselInfo.map((item, index) => {
                return <div onClick={()=>{
                    window.open(item.linkTo)
                }}>
                    <img src={item.imgUrl} className="carousel" alt={item.alt} key={index} />
                </div>;
            })}
        </Carousel>
    }

    componentWillMount() {
        Axios.get('https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/get-carousel').then(res => {
            const { data } = res;
            this.setState({
                carouselInfo: data,
            })
        })

    }
    render() {


        // console.log(this.formRef);
        return (
            <div className="content">
                {this.getCarousel()}
                <div className="knowledge-container">
                    <Layout>
                        <Header style={{ color: '#000', font: 'bold', fontSize: 20, backgroundColor: "#fff", textAlign: 'start', padding: 0, }}>
                            <div style={{ marginLeft: 20 }}>
                                知识共享平台
                            </div>
                        </Header>
                        <Content style={{ padding: '0', flex: 1 }}>
                            <Layout className="site-layout-background" style={{ padding: '24px 0' }}>
                                <Sider className="site-layout-background" width={200}>
                                    <Menu
                                        mode="inline"
                                        defaultSelectedKeys={['1']}
                                        defaultOpenKeys={['sub1']}
                                        style={{ height: '100%' }}
                                    >
                                        <SubMenu key="sub1" icon={<UserOutlined />} title="平台介绍">
                                            <Menu.Item key="1"><Link to="/">平台使用说明</Link></Menu.Item>
                                        </SubMenu>
                                        <SubMenu key="sub2" icon={<LaptopOutlined />} title="通用资源">
                                            <Menu.Item key="5"><Link to="/soft">软件安装教程</Link></Menu.Item>
                                            <Menu.Item key="6"><Link to="/git-list">git命令及解析</Link></Menu.Item>
                                            <Menu.Item key="7"><Link to="/web-info">前端知识库</Link> </Menu.Item>
                                        </SubMenu>
                                        <SubMenu key="sub3" icon={<NotificationOutlined />} title="hzero前端">
                                            <Menu.Item key="9"><Link to="/use-info">入门须知</Link></Menu.Item>
                                            <Menu.Item key="10"><Link to="/hzero-info">hzero-cli详解</Link> </Menu.Item>
                                            <Menu.Item key="11"><Link to="/ui-info">通用UI组件</Link></Menu.Item>
                                            <Menu.Item key="12"><Link to="/util-info">通用Utils</Link></Menu.Item>
                                        </SubMenu>
                                        <SubMenu key="sub1" icon={<UserOutlined />} title="常见问题解决">
                                            <Menu.Item key="113"><Link to="/problem-info">前端常见问题</Link></Menu.Item>
                                        </SubMenu>
                                        <SubMenu key="sub1" icon={<UserOutlined />} title="问题反馈">
                                            <Menu.Item key="20"><Link to="/commit-info">问题反馈与提交</Link></Menu.Item>
                                        </SubMenu>
                                    </Menu>
                                </Sider>
                                <Content style={{ padding: '0 24px', minHeight: 280 }}>
                                    {/* <div dangerouslySetInnerHTML = {{__html:this.state.useInfo.message}} ></div> */}
                                    {/* <Problem /> */}
                                    <HashRouter>
                                        <Switch >
                                            <Route path="/" exact component={Introduce} />
                                            <Route path="/soft" component={SoftInstall} />
                                            <Route path="/git-list" component={GitCommend} />
                                            <Route path="/web-info" component={WebSource} />
                                            <Route path="/use-info" component={UseInfo} />
                                            <Route path="/hzero-info" component={HzeroComponment} />
                                            <Route path="/ui-info" component={UiComponment} />
                                            <Route path="/util-info" component={UtilsComponment} />
                                            <Route path="/problem-info" component={Problem} />
                                            <Route path="/commit-info" component={Commit} />
                                        </Switch>
                                    </HashRouter>
                                </Content>
                            </Layout>
                        </Content>
                    </Layout>
                </div>
            </div>

        )
    }
}
