import React, { Component } from 'react';
import { HeaderComponment } from '../../../../utils/hoc/HeaderComponment';
import Axios from 'axios';
import { BlockTagsAndTitle } from '../../../components/BlockComponent';
import { Input, Button } from 'antd';
import './index.css'

@HeaderComponment({ title: '通用UI组件' })
export default class UiComponment extends Component {

    constructor(props) {
        super(props);
        this.state = {
            uiInfoLists: [],
            inputText: '',
            sourceData: [],
        }
    }

    commonHandler(){
        const {inputText} = this.state;
        let sourceData = this.state.uiInfoLists;
        const Data = sourceData.filter(item=>{
            return item.title.includes(inputText)
        })
        this.setState({
            sourceData: Data,
        })
    }
    inputKeyUp=(e)=>{
        if(e.keyCode===13){
          this.commonHandler();
        }
    }
    componentWillMount() {
        Axios.get('https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/get-ui')
            .then(res => {
                if (res) {
                    this.setState({
                        uiInfoLists: res.data,
                        sourceData: res.data,
                    })
                }
            })
    }
    SearchData(event){
       this.setState({
        inputText: event.target.value,
       })
    }
    render() {
        return (
            <React.Fragment>
                <div className="search">
                    <div style={{width: '50%', display: 'flex'}}>
                        <Input onChange={(target)=>{
                            this.SearchData(target);
                        }}
                        onKeyUp={this.inputKeyUp}
                        />
                        <Button type="primary" onClick={()=>{this.commonHandler()}}>搜索</Button>
                    </div>

                </div>

                {BlockTagsAndTitle(this.state.sourceData)}
            </React.Fragment>
        )
    }
}