import React, { Component } from "react";
import './index.css'
import { Link } from "react-router-dom";
import { Menu } from 'antd';

const { SubMenu } = Menu;
export default class titHeader extends Component {
  render() {
    return (
      <div className="header">
        <Menu onClick={this.handleClick} selectedKeys={['index']} mode="horizontal" className="header-menu" >
          <Menu.Item key="index" >
            <Link to="/" >首页</Link>
        </Menu.Item>
         
          <Menu.Item key="test2">
          码码云
          </Menu.Item>
          <Menu.Item key="test3">
          码码大数据
          </Menu.Item>
          <Menu.Item key="test4">
          码码教育
          </Menu.Item>
          <Menu.Item key="test5">
          企业合作
          </Menu.Item>
          <Menu.Item key="test6">
          个人合作
          </Menu.Item>
          <SubMenu key="SubMenu" title="平台产品">
            <Menu.ItemGroup>
              <Menu.Item key="setting:1"><Link to="/editor-info">富文本编辑器</Link> </Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>
          <Menu.Item key="alipay">
          关于码码
          </Menu.Item>
          <Menu.Item key="test1">
          加入码码
          </Menu.Item>
        </Menu>
      </div>
    )
  }
}