import React, { Component } from 'react'
import Axios from "axios"
import { HeaderComponment } from '../../../../utils/hoc/HeaderComponment'
import './index.css'
import { blockCard } from '../../../components/blockCard';
import { Input, Button } from 'antd';

@HeaderComponment({title: '常见问题解决方法'})
export default class Problem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            problemList: [],
            inputText:'',
            sourceData: [],
        }
    }
    componentWillMount() {
        Axios.get("https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/problems")
            .then(res => {
                if (res) {
                    this.setState({
                        problemList: res.data,
                        sourceData: res.data,
                    })
                }
            })
    }

    SearchData(event){
        this.setState({
         inputText: event.target.value,
        })
    } 
    commonHandler(){
        const {inputText} = this.state;
        let sourceData = this.state.problemList;
        const Data = sourceData.filter(item=>{
            return item.title.includes(inputText)
        })
        this.setState({
            sourceData: Data,
        })
    }
    inputKeyUp=(e)=>{
        if(e.keyCode===13){
          this.commonHandler();
        }
    }
   
    render() {
        return (
         <React.Fragment>
             <div className="search">
                    <div style={{width: '50%', display: 'flex'}}>
                        <Input onChange={(target)=>{
                            this.SearchData(target);
                        }}
                        onKeyUp={this.inputKeyUp}
                        />
                        <Button type="primary" onClick={()=>{this.commonHandler()}}>搜索</Button>
                    </div>

                </div>
          {this.state.sourceData.map(item=>blockCard(item))}
         </React.Fragment>
        )
    }
}