import React, {Component} from 'react'
import { HeaderComponment } from '../../../../utils/hoc/HeaderComponment'
import Axios from 'axios'

@HeaderComponment({title: '平台使用说明'})
export default class Introduce extends Component{
    constructor(props) {
        super(props);
        this.state = {
            useInfo: {},
        }
    }
   render (){
       return (
           <React.Fragment>
               <div dangerouslySetInnerHTML = {{__html:this.state.useInfo.message}} ></div>
           </React.Fragment>
       )
   }

   componentWillMount() {
    Axios.get('https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/introduce-info').then(res => {
        const { data } = res;
        this.setState({
            useInfo: data,
        })
    })
}

}