import React, { Component } from 'react'
import Axios from "axios"
import { HeaderComponment } from '../../../../utils/hoc/HeaderComponment'
import { BlockTagsAndTitle } from '../../../components/BlockComponent';
import './index.css'

@HeaderComponment({title: ' 前端知识库'})
export default class WebSource extends Component {
    constructor(props) {
        super(props);
        this.state = {
            webInfo: [],
        }
    }
    componentWillMount() {
        Axios.get("https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/web-info")
            .then(res => {
                if (res) {
                    this.setState({
                        webInfo: res.data,
                    })
                }
            })
    }
   
    render() {
        return (
         <React.Fragment>
             {BlockTagsAndTitle(this.state.webInfo)}
         </React.Fragment>
        )
    }
}