import React from 'react'
import './hoc.css'

export function HeaderComponment(object = {}) {
    let title = "";
    let className = "";
     
    title = Reflect.has(object, 'title') ?  object.title : '';
    className = Reflect.has(object, "className") ? object.className : '';

    return (WrappedComponent) => {
        return class HeaderCommon extends React.Component {
            render() {
                return (
                    <div className={className === '' ? "hoc-container" : className}>
                        <div className="hoc-title">
                            {title}
                        </div>
                        <div className="hoc-model-container">
                            <WrappedComponent />
                        </div>
                    </div>

                )
            }
        }
    }
}