import React from 'react'
import { Card, Button, Modal } from 'antd'
import { Editor } from 'react-draft-wysiwyg'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
// import { convertToRaw } from 'draft-js';
import draftToHtml from 'draftjs-to-html';

export default class EditorHtml extends React.Component {

    state={
        editorState: '',
        contentState: '',
        showRichText: false
    };

    // 编辑器状态
    onEditorStateChange = (editorState)=> {
        this.setState({editorState})
    };

    // 内容状态
    onContentStateChange = (contentState)=> {
        this.setState({contentState});
    };

    handleClearText = ()=> {
        this.setState({editorState: ''})
    };

    handleGetText = ()=> {
        this.setState({showRichText: true})
    };

    render(){
        const {editorState, contentState, showRichText} = this.state;
        return (
            <div>
               
                <Card title="富文本编辑器" style={{height:"90vh", overflow:"scroll"}} extra={<div>
                    <Button type='primary' onClick={this.handleClearText} style={{marginRight:20}}>清空内容</Button>
                    <Button type='primary' onClick={this.handleGetText}>获取HTML文本</Button>
                </div>}>
                    <Editor
                        editorState={editorState}
                        onContentStateChange={this.onContentStateChange}
                        onEditorStateChange={this.onEditorStateChange}
                    />
                </Card>
                <Modal title='富文本'
                       visible={showRichText}
                       onCancel={()=>{this.setState({showRichText: false})}}
                       footer={null}
                >
                    {draftToHtml(contentState)}
                </Modal>
            </div>
        );
    }
}