import React from 'react'

/**
 * 该组件函数接收一个对象分别为ClassName: 属性值 message: 需要显示的信息
 * 
 * @param {接收一个对象} obj 
 */
 export const WriteHtml = (obj)=><div className={obj.className ? obj.className : ''} dangerouslySetInnerHTML = {{__html:obj.message}} />



 