import React from 'react'
import { Popover, Tag } from 'antd';
import Utils from '../../utils/Utils';
import './block.css'

/**
 * 该函数组要是一个title和tag的block块区
 * 传入参数为数组对象格式为
 * [{title: "不愁码码", tags:[{tag: "IT平台"}],linkUrl: '点击改快去需要跳转的地址' }]
 * @param {数据} datas 
 * @param {isInner} 代表的是不是内部跳转
 */
 export const BlockTagsAndTitle=(datas=[], isInner = true)=>{
    const sourceData = datas;
    return sourceData.map((item, index) => {
        const { tags = [] } = item;
        return <Popover key={index} content={item.title}>
            <div className="block-tag" onClick={() => {
                 window.open(item.linkUrl);
            }} >
                <div style={{ font: "bold", fontSize: 15, marginLeft: 20 }}>{item.title}</div>
                <div>
                    {
                        tags.map((tag, index) => <Tag key={index} color={Utils.getColors()}>{tag.type}</Tag>)
                    }
                </div>
            </div>
        </Popover>
    })
 }