 import React, {Component} from 'react'
 import './index.css'
 import './gitcommend.css'
 import Axios from 'axios'
import { Table } from 'antd'

 export default class GitCommend extends Component{
     
    constructor(props){
        super(props);
        this.state={
        columns: [],
        data: [],
        };
        
    }

    componentWillMount(){
        Axios.get("https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/gitInfo")
             .then(res=>{
                 if(res){
                     const { columns, data} = res.data;
                     this.setState({
                         columns,
                         data,
                     })
                 }
             })
    }
    



    render(){
        return (
            <div className="common-container">
                <div className="common-title">
                    Git及命令行解析
                </div>
                <div className="common-model-container">
                    <Table columns={this.state.columns} className="table-container" dataSource={this.state.data}/>
                </div>

            </div>
        )
    }
 }