import React, { Component } from "react"
import Axios from 'axios'
import './softinstall.css'
import './index.css'

export default class SoftInstall extends Component {

    constructor(props) {
        super(props)
        this.state = {
            softList: [],
        }
    }

    componentWillMount() {
        Axios.get('https://www.fastmock.site/mock/38b63f3a46203fadc7c390612ad9a2ba/nomama/soft-install')
            .then(res => {
                if (res) {
                    this.setState({
                        softList: res.data,
                    })
                }
            })
    }

    /**
     * 跳转
     * @param {跳转的链接} linkTo 
     */
    linkToUrl(linkTo){
       window.open(linkTo);
    }

    getSoftData() {
        const softSourceData = this.state.softList;
        return softSourceData.map((item, index) => {
            return (
                <div className="soft-model"  key={index} onClick ={()=>{
                    this.linkToUrl(item.linkUrl)
                }}>

                    <div className="soft-model-top">
                        <img src={item.img} width="100%" height="100%" className="soft-img" alt={item.title} />
                    </div>
            <div className="soft-model-title">{item.title}</div>
                </div>
            )
        })
    }

    render() {
        return (
            <div className="common-container">
                <div className="common-title">
                    软件安装教程
                </div>
                <div className="common-model-container">
                   {this.getSoftData()}
                </div>

            </div>
        )
    }

}